@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400..700&family=Raleway:ital,wght@0,100..900;1,100..900&display=swap');

/* Tailwind Directives */
@tailwind base;
@tailwind components;
@tailwind utilities;

/* Global Reset and Styling */
*,
*::before,
*::after {
  box-sizing: border-box;
}

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Raleway', sans-serif; /* Default font */
}

/* Apply Dancing Script font to specific sections */
h1, h2, h3, .font-serif {
  font-family: 'Dancing Script', cursive;
}

/* Optional: Body Font Size Adjustments */
body {
  font-size: 1rem; /* Adjust the base font size for better readability */
}

/* Make sure the HTML takes full height */
html {
  height: 100%;
}

/* Tailwind Overrides (Optional) */
body {
  background-color: #000; /* Make the background black by default */
  color: white; /* Set default text color to white */
}

/* You can add more custom styles here if necessary */
